'use client';
import { useAuth } from '../../components/AuthContext';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';

export default function Dashboard() {
  const { user, loading, logout } = useAuth();
  const router = useRouter();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    if (!loading && !user) {
      router.push('/');
    }
  }, [user, loading, router]);

  if (loading || !user) return <div className="p-8">Please wait...</div>;

  return (
    <div className="relative flex min-h-screen w-full flex-col overflow-x-hidden bg-[#f9f9f9] text-[#111418] dark:bg-[#101922] dark:text-white transition-colors duration-200">
      
      {/* Top Navigation Bar */}
      <header className="sticky top-0 z-40 w-full border-b border-gray-200 bg-white/95 backdrop-blur dark:bg-[#1a2632]/95 dark:border-gray-800">
        <div className="mx-auto flex h-16 max-w-7xl items-center justify-between px-4 sm:px-6 lg:px-8">
          
          {/* Left: Hamburger & Logo */}
          <div className="flex items-center gap-4">
            <button 
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="flex items-center justify-center rounded-lg p-2 text-gray-500 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-800 transition-colors"
            >
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="4" x2="20" y1="12" y2="12"/><line x1="4" x2="20" y1="6" y2="6"/><line x1="4" x2="20" y1="18" y2="18"/></svg>
            </button>
            <div className="flex items-center gap-2">
              <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-[#2b8cee] text-white">
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M21 12V7H5a2 2 0 0 1 0-4h14v4"/><path d="M3 5v14a2 2 0 0 0 2 2h16v-5"/><path d="M18 12a2 2 0 0 0 0 4h4v-4Z"/></svg>
              </div>
              <h1 className="font-sans text-xl font-bold tracking-tight">Hisab</h1>
            </div>
          </div>

          {/* Right: Actions & Avatar */}
          <div className="flex items-center gap-4">
            <button className="hidden sm:flex items-center justify-center rounded-full p-2 text-gray-500 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-gray-800 transition-colors">
               <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M6 8a6 6 0 0 1 12 0c0 7 3 9 3 9H3s3-2 3-9"/><path d="M10.3 21a1.94 1.94 0 0 0 3.4 0"/></svg>
            </button>
            <div className="h-10 w-10 overflow-hidden rounded-full border border-gray-200 dark:border-gray-700 bg-gray-100 dark:bg-gray-800 relative group cursor-pointer" onClick={logout}>
               {user.photoURL ? (
                  <img alt="Profile" className="h-full w-full object-cover" src={user.photoURL} />
               ) : (
                  <div className="h-full w-full flex items-center justify-center bg-blue-100 text-blue-600 font-bold text-lg">
                    {user.displayName ? user.displayName[0] : 'U'}
                  </div>
               )}
               <div className="absolute inset-0 bg-black/20 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center text-white text-xs font-bold">Logout</div>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content Area */}
      <main className="flex-1 w-full max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        
        {/* Greeting Header */}
        <div className="mb-10 flex flex-col gap-2">
            <h2 className="font-sans text-3xl md:text-4xl font-extrabold tracking-tight">
                Good evening, {user.displayName?.split(' ')[0]}
            </h2>
            <p className="text-base text-gray-500 dark:text-gray-400">
                Here’s your shared expenses overview for this month.
            </p>
        </div>

        {/* Quick Stats Summary */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-10">
            <div className="rounded-xl bg-white dark:bg-[#1a2632] p-6 shadow-sm border border-gray-100 dark:border-gray-800 flex items-center justify-between">
                <div>
                    <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Total you owe</p>
                    <p className="mt-1 text-2xl font-bold text-red-600 dark:text-red-400">₹4,250</p>
                </div>
                <div className="h-12 w-12 rounded-full bg-red-50 dark:bg-red-900/20 flex items-center justify-center text-red-600 dark:text-red-400">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="7" y1="17" x2="17" y2="7"/><polyline points="7 7 17 7 17 17"/></svg>
                </div>
            </div>
            <div className="rounded-xl bg-white dark:bg-[#1a2632] p-6 shadow-sm border border-gray-100 dark:border-gray-800 flex items-center justify-between">
                <div>
                    <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Total owed to you</p>
                    <p className="mt-1 text-2xl font-bold text-emerald-600 dark:text-emerald-400">₹1,850</p>
                </div>
                <div className="h-12 w-12 rounded-full bg-emerald-50 dark:bg-emerald-900/20 flex items-center justify-center text-emerald-600 dark:text-emerald-400">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="17" y1="7" x2="7" y2="17"/><polyline points="17 17 7 17 7 7"/></svg>
                </div>
            </div>
        </div>

        {/* Groups Section */}
        <section className="mb-12">
            <div className="flex items-center justify-between mb-5">
                <h3 className="text-xl font-bold">Groups</h3>
                <button className="text-sm font-semibold text-[#2b8cee] hover:text-blue-600 flex items-center gap-1">
                    View all 
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="5" y1="12" x2="19" y2="12"/><polyline points="12 5 19 12 12 19"/></svg>
                </button>
            </div>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-5">
                {/* Card 1 */}
                <div className="group flex flex-col rounded-xl border border-gray-200 dark:border-gray-800 bg-white dark:bg-[#1a2632] p-5 shadow-sm transition-all hover:shadow-md hover:border-blue-500/30 cursor-pointer">
                    <div className="flex items-start justify-between mb-4">
                        <div className="h-12 w-12 rounded-lg bg-gradient-to-br from-orange-400 to-pink-500 flex items-center justify-center text-white shadow-sm">
                             <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M5 12h14"/><path d="M12 5v14"/></svg> {/* Placeholder Icon */}
                        </div>
                        <span className="inline-flex items-center rounded-full bg-red-100 dark:bg-red-900/30 px-2.5 py-0.5 text-xs font-medium text-red-700 dark:text-red-300">
                            You owe ₹2,500
                        </span>
                    </div>
                    <h4 className="text-lg font-bold mb-1 group-hover:text-[#2b8cee] transition-colors">Goa Trip</h4>
                    <div className="flex items-center gap-2 text-sm text-gray-500">
                        <span>5 members</span>
                        <span className="text-gray-300">•</span>
                        <span>Last updated 2h ago</span>
                    </div>
                </div>

                {/* Card 2 */}
                <div className="group flex flex-col rounded-xl border border-gray-200 dark:border-gray-800 bg-white dark:bg-[#1a2632] p-5 shadow-sm transition-all hover:shadow-md hover:border-blue-500/30 cursor-pointer">
                    <div className="flex items-start justify-between mb-4">
                        <div className="h-12 w-12 rounded-lg bg-gradient-to-br from-blue-400 to-indigo-500 flex items-center justify-center text-white shadow-sm">
                             <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="m3 9 9-7 9 7v11a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2z"/><polyline points="9 22 9 12 15 12 15 22"/></svg>
                        </div>
                        <span className="inline-flex items-center rounded-full bg-emerald-100 dark:bg-emerald-900/30 px-2.5 py-0.5 text-xs font-medium text-emerald-700 dark:text-emerald-300">
                            Owed ₹400
                        </span>
                    </div>
                    <h4 className="text-lg font-bold mb-1 group-hover:text-[#2b8cee] transition-colors">Roommates</h4>
                    <div className="flex items-center gap-2 text-sm text-gray-500">
                        <span>3 members</span>
                        <span className="text-gray-300">•</span>
                        <span>Last updated 1d ago</span>
                    </div>
                </div>
            </div>
        </section>

      </main>

      {/* Mobile Menu Drawer */}
       {mobileMenuOpen && (
        <div className="fixed inset-0 z-50 flex lg:hidden">
          {/* Backdrop */}
          <div 
            className="fixed inset-0 bg-black/20 backdrop-blur-sm transition-opacity" 
            onClick={() => setMobileMenuOpen(false)}
          ></div>
          
          {/* Drawer Panel */}
          <div className="relative w-[280px] h-full bg-white dark:bg-[#1a2632] shadow-2xl flex flex-col p-6 animate-in slide-in-from-left-full duration-200">
             <div className="flex items-center justify-between mb-8">
                 <h2 className="text-xl font-bold">Menu</h2>
                 <button onClick={() => setMobileMenuOpen(false)} className="p-2 -mr-2 text-gray-500 hover:bg-gray-100 rounded-full">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="18" y1="6" x2="6" y2="18"/><line x1="6" y1="6" x2="18" y2="18"/></svg>
                 </button>
             </div>
             
             <div className="flex items-center gap-3 mb-8 p-3 bg-gray-50 dark:bg-gray-800 rounded-xl">
                {user.photoURL ? (
                    <img alt="Profile" className="h-10 w-10 rounded-full object-cover" src={user.photoURL} />
                ) : (
                    <div className="h-10 w-10 flex items-center justify-center rounded-full bg-blue-100 text-blue-600 font-bold">
                        {user.displayName ? user.displayName[0] : 'U'}
                    </div>
                )}
                <div>
                    <p className="font-bold text-sm">{user.displayName}</p>
                    <p className="text-xs text-gray-500 truncate max-w-[150px]">{user.email}</p>
                </div>
             </div>

             <nav className="flex-1 space-y-2">
                 <button className="w-full flex items-center gap-3 px-4 py-3 bg-blue-50 text-blue-600 rounded-xl font-semibold">
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><rect width="7" height="9" x="3" y="3" rx="1"/><rect width="7" height="5" x="14" y="3" rx="1"/><rect width="7" height="9" x="14" y="12" rx="1"/><rect width="7" height="5" x="3" y="16" rx="1"/></svg>
                    Dashboard
                 </button>
                 <button className="w-full flex items-center gap-3 px-4 py-3 text-gray-600 hover:bg-gray-50 rounded-xl font-semibold transition-colors">
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M16 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"/><circle cx="8.5" cy="7" r="4"/><line x1="20" y1="8" x2="20" y2="14"/><line x1="23" y1="11" x2="17" y2="11"/></svg>
                    New Group
                 </button>
             </nav>

             <div className="pt-4 border-t border-gray-100 dark:border-gray-800">
                <button onClick={logout} className="w-full flex items-center gap-3 px-4 py-3 text-red-600 hover:bg-red-50 rounded-xl font-semibold transition-colors">
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"/><polyline points="16 17 21 12 16 7"/><line x1="21" x2="9" y1="12" y2="12"/></svg>
                    Log Out
                </button>
             </div>
          </div>
        </div>
      )}

      {/* Floating Action Button */}
      <button className="fixed bottom-8 right-8 z-50 flex h-14 w-auto items-center gap-2 rounded-full bg-[#2b8cee] px-6 text-white shadow-lg shadow-blue-500/30 hover:bg-blue-600 hover:shadow-xl hover:scale-105 active:scale-95 transition-all duration-200">
         <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><line x1="12" y1="5" x2="12" y2="19"/><line x1="5" y1="12" x2="19" y2="12"/></svg>
         <span className="font-bold text-sm tracking-wide">Add Spend</span>
      </button>

    </div>
  );
}
